@media screen and (min-width: map-get($map: $screen-sizes, $key: "xs")) and (max-width: (map-get($map: $screen-sizes, $key: "sm") - 1px)) {
    html {
        font-size: 16px;
    }

    /* ============================= SECTIONS ============================= */

    // sections/_landing.scss
    #logo {
    }

    #nav-items {
        height: 25%;
        flex-direction: column;
        justify-content: space-between;
        align-content: center;
        align-items: center;
    }

    // sections/_suppliers.scss

    #suppliers-section {
        #suppliers-container {
            grid-template-columns: 1fr;
            .supplier {
                &.expanded {
                    grid-column: span 1;
                    grid-row: span 2;
                }
            }
        }
    }

    // sections/_about.scss

    #about-section {
        #about-content-container {
            .slick-vertical {
                .slick-arrow {
                    &.slick-prev {
                        left: 48%;
                    }

                    &.slick-next {
                        left: 48%;
                    }
                }
            }
        }
    }

    // sections/_contact.scss
    #contact-section {
        #hours {
            .hours-entry {
                font-size: 0.8rem;
            }
        }

        #contact-form {
            input,
            textarea {
                &[type="submit"] {
                    width: 50% !important;
                }
            }
        }
    }

    /* ============================= SHARED ============================= */

    // shared/_divider.scss

    // shared/_footer.scss
    #footer {
        flex-direction: column;
    }
}
